.page-header {
  width: 100%;
  background-color: $color-fourth;
}

.page-header__wrapper {
  max-width: 1140px;
  margin: 0 auto;
  padding-top: 20px;
  padding-bottom: 20px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;

  @include respond-above (1179px) {
    padding-left: 20px;
    padding-right: 20px;
    margin: 0;
  }

  @include respond-above ($breakpoints-tablet) {
    display: block;
  }

  @include respond-above ($breakpoints-mobile) {
    min-width: 320px;
    padding-left: 16px;
    padding-right: 16px;
    padding-top: 16px;
    padding-bottom: 16px;
  }
}

.page-header__container {
  width: 154px;

  @include respond-above ($breakpoints-tablet) {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    width: 100%;
  }
}

.page-header__container button {
  border: 0;
  padding: 0;

  background-color: inherit;
  cursor: pointer;


  &:hover {
    opacity: 0.7;
  }

  &:active {
    opacity: 0.5;
  }
}

.page-header__logo {
  width: 154px;

  @include respond-above ($breakpoints-tablet) {
    width: 138px;
  }
}

.page-header__menu {
  display: none;

  @include respond-above ($breakpoints-tablet) {
    display: block;
    width: 28px;
    height: 28px;
    background-image: url("../img/menu.svg");
    background-size: 24px 24px;
    background-position: 2px 2px;
    background-repeat: no-repeat;
  }
}

.page-header__close {
  display: none;

  @include respond-above ($breakpoints-tablet) {
    display: none;
    width: 28px;
    height: 28px;
    background-image: url("../img/close-menu.svg");
    background-size: 28px 28px;
    background-position: 0 0;
    background-repeat: no-repeat;
  }
}

.page-header__logo svg {
  width: 154px;
  height: 50px;

  @include respond-above ($breakpoints-tablet) {
    width: 138px;
    height: 46px;
  }
}
