.modal {
  display: none;
  width: 352px;
  border-radius: 10px;
  background-color: $color-first;
  padding-top: 15px;
  padding-bottom: 15px;
  padding-left: 24px;
  padding-right: 44px;
  position: absolute;
  top: -92px;
  left: 30px;
  z-index: 100;

  @include respond-above ($breakpoints-desktop-wide) {
    top: -102px;
    left: -49px;
  }

  @include respond-above (1800px) {
    top: -104px;
  }

  @include respond-above (1600px) {
    top: -110px;
  }

  @include respond-above (1400px) {
    top: -116px;
  }

  @include respond-above (1140px) {
    top: -123px;
  }

  @include respond-above ($breakpoints-tablet) {
    top: -125px;
    left: -76px;
  }

  @include respond-above ($breakpoints-mobile) {
    width: 320px;
    left: -141px;
    top: -153px;
  }

  @include respond-above (500px) {
    top: -159px;
  }

  @include respond-above (429px) {
    top: -140px;
  }

  @include respond-above (350px) {
    left: -160px;
  }
}

.modal:after {
  @include icon;

  display: block;
  position: absolute;
  top: 16px;
  right: 44px;
  width: 44px;
  height: 44px;
  background-image: url("../img/electro-voice.svg");
  background-position: 11px 8px;
  background-size: 22px 28px;
  background-color: $color-third;
  border-radius: 8px;
  box-shadow: 4px 6px 12px rgba(57, 125, 255, 0.12);

  @include respond-above ($breakpoints-mobile) {
    width: 38px;
    height: 38px;
    background-position: 10px 7px;
    background-size: 18px 25px;
    right: 40px;
  }
}

.modal button {
  @include button;

  background-color: transparent;
  width: 20px;
  height: 20px;
  padding: 2px;
  cursor: pointer;
  position: absolute;
  right: 12px;
  top: 16px;
}

.modal button svg {
  stroke: $color-second;
}

.modal button svg:hover {
  stroke: $color-third;
}

.modal button svg:active {
  stroke: $color-third;
  opacity: 0.5;
}

.modal h2 {
  @include content;
  @include montserrat(18px, $color-second, 500, 24px);
  max-width: 155px;
  margin-bottom: 10px;

  @include respond-above ($breakpoints-mobile) {
    font-size: 16px;
    line-height: 21px;
    max-width: 144px;
  }
}

.modal__list {
  @include list;
}

.modal__list li {
  @include montserrat(12px, $color-second, 400, 17px);

  padding-left: 24px;
  position: relative;

  @include respond-above ($breakpoints-mobile) {
    font-size: 12px;
    line-height: 17px;
  }
}

.modal__list li::before {
  @include icon;

  position: absolute;
  display: block;
  width: 16px;
  height: 16px;
  border-radius: 50%;
  left: 0;
  top: 0;
  background-color: $color-third;
  opacity: 0.15;
}

.modal__list li::after {
  @include icon;

  position: absolute;
  display: block;
  width: 6px;
  height: 6px;
  border-radius: 50%;
  left: 5px;
  top: 5px;
  background-color: $color-third;
}

.modal__list li:not(:last-child) {
  margin-bottom: 10px;
}
