.slider-first__content h3 {
  @include content;
  @include montserrat(18px, $color-first, 500, 22px);
  margin-bottom: 10px;
  max-width: 620px;

  @include respond-above (1179px) {
    max-width: 430px;
  }

  @include respond-above ($breakpoints-tablet) {
    font-size: 14px;
    line-height: 17px;
    font-weight: 400;
    max-width: 355px;
  }
}

.slider-first__content h2 {
  @include content;
  @include montserrat(52px, $color-first, 600, 63px);
  margin-bottom: 25px;
  max-width: 620px;

  @include respond-above (1179px) {
    max-width: 430px;
  }

  @include respond-above ($breakpoints-tablet) {
    font-size: 30px;
    line-height: 37px;
    max-width: 355px;
  }
}

.slider-first__content p {
  @include content;
  @include montserrat(16px, rgba(255, 255, 255, 0.8), 500, 24px);
  margin-bottom: 54px;
  max-width: 620px;

  @include respond-above (1179px) {
    max-width: 430px;
  }

  @include respond-above ($breakpoints-tablet) {
    font-size: 14px;
    line-height: 21px;
    margin-bottom: 30px;
    max-width: 355px;
  }
}

.slider-first__buttons {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  width: 395px;

  @include respond-above ($breakpoints-tablet) {
    width: 100%;
    max-width: 350px;
  }

  @include respond-above (374px) {
    flex-direction: column;
  }
}

.slider-first__buttons button {
  @include button;
  @include montserrat(16px, $color-first, 500, 20px);

  width: 186px;
  text-align: center;
  border-radius: 8px;
  padding-top: 13px;
  padding-bottom: 13px;
  cursor: pointer;

  @include respond-above ($breakpoints-tablet) {
    font-size: 14px;
    line-height: 17px;
    width: 158px;
    padding-top: 11px;
    padding-bottom: 11px;
  }
}

.slider-first__buttons button:first-child {
  border: 1px solid $color-first;
  background-color: transparent;

  @include respond-above ($breakpoints-tablet) {
    font-weight: 400;
  }

  @include respond-above (374px) {
    margin-bottom: 15px;
  }
}

.slider-first__buttons button:last-child {
  border: 1px solid $color-third;
  background-color: $color-third;
}

.slider-first__buttons button:first-child:hover {
  border: 1px solid $color-third;
  background-color: $color-third;
}

.slider-first__buttons button:first-child:active {
  border: 1px solid $color-third;
  background-color: $color-third;
  opacity: 0.5;
}

.slider-first__buttons button:last-child:hover {
  border: 1px solid $color-first;
  background-color: transparent;
}

.slider-first__buttons button:last-child:active {
  border: 1px solid $color-first;
  background-color: transparent;
  opacity: 0.5;
}

.slider-first__content {
  max-width: 1140px;
  margin: 0 auto;
  padding-top: 120px;
  padding-bottom: 208px;

  @include respond-above (1179px) {
    padding-left: 20px;
    padding-right: 20px;
    margin: 0;
  }

  @include respond-above ($breakpoints-tablet) {
    min-width: 320px;
    padding-left: 16px;
    padding-right: 16px;
    padding-top: 36px;
    padding-bottom: 60px;
  }
}

.slider-first__slide {
  width: 100%;
}

.slider-first__slide--first {
  background-image: url("../img/conference-hall-1x.jpg");
  background-position: 0 0;
  background-repeat: no-repeat;
  background-size: 100vw auto;

  @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
    background-image: url("../img/conference-hall-2x.jpg");
  }

  @include respond-above (1919px) {
    background-size: auto 100%;
  }

  @include respond-above ($breakpoints-mobile) {
    background-image: url("../img/slider.jpg");
    background-size: 100vw auto;

    @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
      background-image: url("../img/slider-2x.jpg");
    }
  }

  @include respond-above (374px) {
    background-size: auto 100%;
  }
}

.slider-first__slide--second {
  background-image: url("../img/conference-hall-1x.jpg");
  background-position: 0 0;
  background-repeat: no-repeat;
  background-size: 100vw auto;

  @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
    background-image: url("../img/conference-hall-2x.jpg");
  }

  @include respond-above (1919px) {
    background-size: auto 100%;
  }

  @include respond-above ($breakpoints-mobile) {
    background-image: url("../img/slider.jpg");
    background-size: 100vw auto;

    @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
      background-image: url("../img/slider-2x.jpg");
    }
  }

  @include respond-above (374px) {
    background-size: auto 100%;
  }
}
