@mixin list {
  margin: 0;
  padding: 0;
  list-style: none;
}

@mixin content {
  margin: 0;
  padding: 0;
}

@mixin button {
  display: block;
  border: 0;
  padding: 0;
}

@mixin montserrat($size: false, $colour: false, $weight: false,  $lh: false) {
  font-family: "Montserrat", "Arial", sans-serif;
  font-style: normal;

  @if $size {
    font-size: $size;
  }

  @if $colour {
    color: $colour;
  }

  @if $weight {
    font-weight: $weight;
  }

  @if $lh {
    line-height: $lh;
  }
}

@mixin icon {
  background-position: 0 0;
  background-repeat: no-repeat;
  content: "";
}

@mixin pseudo ($display: block, $pos: absolute) {
  display: $display;
  position: $pos;
}

@mixin respond-above ($breakpoint) {
  @media screen and (max-width: $breakpoint) {
    @content;
  }
}

@mixin wrapper {
  max-width: 1920px;
  margin: 0 auto;
  padding-left: 35px;
  padding-right: 80px;

  @include respond-above ($breakpoints-desktop) {
    padding-left: 20px;
    padding-right: 40px;
    margin: 0;
  }

  @include respond-above ($breakpoints-mobile) {
    min-width: 320px;
    padding-left: 0;
    padding-right: 0;
  }
}
