.electro-voice {
  width: 100%;
  margin-top: 165px;
  margin-bottom: 165px;

  @include respond-above ($breakpoints-tablet) {
    margin-top: 50px;
    margin-bottom: 50px;
  }
}

.electro-voice__wrapper {
  max-width: 2050px;
  margin: 0 auto;
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;

  @include respond-above (1920px) {
    min-width: 320px;
  }
}

.electro-voice__picture {
  width: 50%;
  display: block;
  max-width: 960px;
}

.electro-voice__picture--first img{
  width: 100%;
  height: auto;
}

.electro-voice__cabinet img {
  width: 100%;
  height: auto;
}

.electro-voice__picture--second {
  position: relative;
}

.electro-voice__left {
  @include icon;

  position: absolute;
  display: block;
  width: 73px;
  height: 73px;
  top: 40px;
  left: 172px;
  z-index: 10;
  cursor: pointer;

  @include respond-above (1919px) {
    top: calc(8% - 17px);
    left: 17%;
  }

  @include respond-above (767px) {
    width: 40px;
    height: 40px;
  }

  @include respond-above (429px) {
    top: 14px;
    right: 10px;
  }
}

.electro-voice__left:before {
  @include icon;

  display: block;
  position: relative;
  height: 20px;
  left: 23px;
  bottom: -52px;
  width: 20px;
  background-color: transparent;

  @include respond-above ($breakpoints-mobile) {
    left: 10px;
    bottom: -25px;
  }
}

.electro-voice__left img {
  width: 100%;
  height: auto;
}

.electro-voice__right {
  @include icon;

  position: absolute;
  display: block;
  width: 73px;
  height: 73px;
  top: 63px;
  right: 198px;
  z-index: 9;

  @include respond-above (1919px) {
    top: 8%;
    right: 17%;
  }

  @include respond-above (767px) {
    width: 40px;
    height: 40px;
  }

  @include respond-above (429px) {
    top: 32px;
    right: 10px;
  }
}

.electro-voice__right img {
  width: 100%;
  height: auto;
}

.electro-voice__left:hover {
  transform: scale(1.04);
}
