.navigation {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  width: 84%;

  @include respond-above (1139px) {
    width: 83%;
  }

  @include respond-above ($breakpoints-tablet) {
    display: none;
    width: calc(100% - 32px);
    margin-top: 25px;
    position: fixed;
    background-color: $color-fourth;
    top: 82px;
    left: 16px;
    height: 100%;
    z-index: 105;
  }
}


.navigation__list {
  @include list;

  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  width: 65%;

  @include respond-above (1139px) {
    width: 64%;
  }

  @include respond-above ($breakpoints-tablet) {
    display: block;
    width: 100%;
    margin-bottom: 32px;
  }
}

.navigation__contacts {
  @include list;

  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  width: 32%;
  align-items: center;

  @include respond-above (1139px) {
    width: 35%;
  }

  @include respond-above ($breakpoints-tablet) {
    display: block;
    width: 100%;
  }
}

.navigation__contacts a:hover {
  opacity: 0.7;
}

.navigation__contacts a:active {
  opacity: 0.5;
}

.navigation__item {
  position: relative;
}

.navigation__item a {
  @include montserrat(14px, rgba(43, 45, 58, 0.7), 400);

  @include respond-above ($breakpoints-tablet) {
    font-size: 18px;
    color: $color-second;
    font-weight: 500;
    letter-spacing: 0.01em;
    padding-top: 24px;
    padding-bottom: 24px;
    padding-left: 48px;
    padding-right: 24px;
    border-bottom: 1px solid rgba(43, 45, 58, 0.1);
  }
}

.navigation__item--active a {
  color: $color-third;
  font-weight: 600;
}

.navigation__item a:before {
  display: none;

  @include respond-above ($breakpoints-tablet) {
    display: block;
    width: 24px;
    height: 24px;
    position: absolute;
    background-size: 24px 24px;
    @include icon;

    left: 0;
    top: 20px;
  }
}

.navigation__item--first a:before {
  background-image: url("../img/home.svg");
}

.navigation__item--second a:before {
  background-image: url("../img/projects.svg");
}

.navigation__item--third a:before {
  background-image: url("../img/lamp.svg");
}

.navigation__item--fourth a:before {
  background-image: url("../img/about-company.svg");
}

.navigation__item--fifth a:before {
  background-image: url("../img/gotovie-resheniya.svg");
}

.navigation__item--sixth a:before {
  background-image: url("../img/map-pin.svg");
}

.navigation__button {
  display: none;

  @include respond-above ($breakpoints-tablet) {
    @include button;

    width: 100%;
    max-width: 350px;
    box-shadow: 0 5.12px 20.48px rgba(85, 104, 255, 0.12);
    border-radius: 6px;
    background-color: $color-third;
    @include montserrat(16px, $color-first, 500);

    padding-top: 12px;
    padding-bottom: 12px;
    padding-left: 12px;
    padding-right: 12px;
    text-align: center;
    margin-bottom: 30px;
    cursor: pointer;
  }
}

.navigation__button:hover {
  opacity: 0.7;
}

.navigation__button:active {
  opacity: 0.5;
}

.navigation__mail span {
  @include montserrat(13px, $color-second, 600);

  @include respond-above ($breakpoints-tablet) {
    @include montserrat(18px, $color-third, 500);
  }
}

.navigation__mail span:first-child {
  display: none;

  @include respond-above ($breakpoints-tablet) {
    display: block;
    font-size: 22px;
    color: $color-second;
    margin-bottom: 12px;
  }
}

.navigation__mail span:last-child {
  padding-left: 30px;
  position: relative;

  @include respond-above ($breakpoints-tablet) {
    padding-left: 38px;
  }
}

.navigation__mail span:last-child::before {
  @include icon;

  display: block;
  width: 24px;
  height: 24px;
  position: absolute;
  left: 0;
  top: -4px;
  background-image: url("../img/mail.svg");
  background-size: 24px 24px;
}

.navigation__telephone {
  display: block;

  @include respond-above ($breakpoints-tablet) {
    display: none;
  }
}

.navigation__telephone span {
  display: block;
  @include montserrat(13px, $color-second, 600);
  position: relative;
  padding-left: 35px;
}

.navigation__telephone span:first-child {
  @include montserrat(11px, $color-third, 400);
}

.navigation__telephone span:last-child::before {
  @include icon;

  display: block;
  width: 24px;
  height: 24px;
  position: absolute;
  left: 0;
  top: -10px;
  background-image: url("../img/telephone.svg");
  background-size: 24px 24px;
}

.navigation__item--active {
  position: relative;
}

.navigation__item--active::after {
  position: absolute;
  display: block;
  width: 83%;
  height: 3px;
  box-shadow: 0px -1px 2px rgba(40, 88, 179, 0.15);
  border-radius: 6px;

  @include icon;

  bottom: -38px;
  left: 4px;
  background-color: $color-third;


  @include respond-above ($breakpoints-tablet) {
    display: none;
  }
}

.navigation__item:hover::after {
  position: absolute;
  display: block;
  width: 83%;
  height: 3px;
  box-shadow: 0px -1px 2px rgba(40, 88, 179, 0.15);
  border-radius: 6px;

  @include icon;

  bottom: -37px;
  left: 4px;
  background-color: $color-third;


  @include respond-above ($breakpoints-tablet) {
    display: none;
  }
}

.navigation__item a:hover {
  color: $color-third;
}

.navigation__item:active::after {
  position: absolute;
  display: block;
  width: 83%;
  height: 3px;
  box-shadow: 0px -1px 2px rgba(40, 88, 179, 0.15);
  border-radius: 6px;

  @include icon;

  bottom: -37px;
  left: 4px;
  background-color: $color-third;
  opacity: 0.5;


  @include respond-above ($breakpoints-tablet) {
    display: none;
  }
}

.navigation__item a:active {
  color: $color-third;
  opacity: 0.5;
}
