.popup__wrap {
  display: block;
  width: 538px;
  padding-left: 42px;
  padding-right: 24px;
  padding-top: 16px;
  padding-bottom: 48px;
  background-color: $color-first;
  border-radius: 16px;

  @include respond-above ($breakpoints-mobile) {
    max-width: 343px;
    width: 100%;
    padding-left: 16px;
    padding-right: 16px;
    padding-bottom: 32px;
    border-radius: 12px;
  }
}

.popup__close {
  @include button;

  margin-left: auto;
  width: 18px;
  height: 18px;
  background-color: transparent;
  cursor: pointer;
  margin-bottom: 6px;

  @include respond-above ($breakpoints-mobile) {
    width: 14px;
    height: 14px;
    margin-bottom: 8px;
  }
}

.popup__close svg {
  width: 18px;
  height: 18px;
  stroke: $color-second;

  @include respond-above ($breakpoints-mobile) {
    width: 14px;
    height: 14px;
  }
}

.popup__close svg:hover {
  stroke: $color-third;
}

.popup__close svg:active {
  stroke: $color-third;
  opacity: 0.5;
}

.popup__wrapper {
  padding-right: 18px;

  @include respond-above ($breakpoints-mobile) {
    padding-right: 0;
  }
}

.popup__wrapper h2 {
  @include content;
  @include montserrat(24px, $color-second, 600, 29px);
  margin-bottom: 10px;

  @include respond-above ($breakpoints-mobile) {
    font-size: 21px;
    line-height: 26px;
  }
}

.popup__wrapper p {
  @include content;
  @include montserrat(16px, $color-second, 400, 26px);
  margin-bottom: 25px;

  @include respond-above ($breakpoints-mobile) {
    font-size: 12px;
    line-height: 19px;
    margin-bottom: 20px;
  }
}

.popup__container {
  display: block;
}

.popup__container input {
  display: block;
  width: 100%;
  padding-top: 14px;
  padding-bottom: 14px;
  padding-left: 14px;
  padding-right: 14px;
  border: 1px solid rgba(0, 0, 0, 0.2);
  border-radius: 9px;
  background-color: inherit;
  @include montserrat(16px, $color-second, 400, 18px);

  @include respond-above ($breakpoints-mobile) {
    padding-top: 12px;
    padding-bottom: 12px;
    padding-left: 12px;
    padding-right: 12px;
    font-size: 14px;
    line-height: 16px;
  }
}

.popup__container input:hover {
  border: 1px solid $color-third;
  background-color: #f2f4f7;
}

.popup__container input:focus {
  outline: 1px solid $color-third;
  background-color: #f2f4f7;
}

.popup__container input::placeholder {
  @include montserrat(16px, rgba(43, 45, 58, 0.5), 400, 18px);

  @include respond-above ($breakpoints-mobile) {
    font-size: 14px;
    line-height: 16px;
  }
}

.popup__container:not(:last-child) {
  margin-bottom: 24px;

  @include respond-above ($breakpoints-mobile) {
    margin-bottom: 16px;
  }
}

.popup__agreement {
  display: block;
  margin-top: 24px;
  margin-bottom: 15px;
  width: 100%;

  @include respond-above ($breakpoints-mobile) {
    margin-top: 19px;
    margin-bottom: 19px;
  }
}

.popup__agreement label {
  @include montserrat(14px, $color-second, 400, 17px);
  display: flex;
  flex-wrap: wrap;
  padding-left: 24px;
  position: relative;
  cursor: pointer;

  @include respond-above ($breakpoints-mobile) {
    font-size: 9px;
    line-height: 11px;
  }
}

.popup__agreement label a {
  color: $color-third;
  display: inline-block;
  margin-left: 3px;
}

.popup__agreement label a:hover {
  opacity: 0.7;
}

.popup__agreement label a:active {
  opacity: 0.5;
}

.popup__agreement input {
  position: absolute;
  opacity: 0;
  pointer-events: none;
}

.popup__agreement input:checked + label::before {
  @include icon;

  display: block;
  width: 10px;
  height: 7px;
  position: absolute;
  background-image: url("../img/checkbox.svg");
  background-size: 10px 7px;
  top: 4px;
  left: 3px;
  z-index: 90;

  @include respond-above ($breakpoints-mobile) {
    top: 2px;
  }
}

.popup__agreement input + label::after {
  @include icon;

  display: block;
  width: 15px;
  height: 15px;
  position: absolute;
  top: 0;
  left: 0;
  border-radius: 3px;
  border: 1px solid $color-third;
  background: $color-fourth;

  @include respond-above ($breakpoints-mobile) {
    top: -2px;
  }
}

.popup__agreement input:hover + label::after {
  background: $color-first;
}

.popup__button {
  display: block;
  width: 100%;
}

.popup__button button {
  @include button;
  @include montserrat(18px, $color-first, 600, 22px);

  width: 100%;
  padding: 15px;
  text-align: center;
  background-color: $color-third;
  cursor: pointer;
  border-radius: 8px;

  @include respond-above ($breakpoints-mobile) {
    @include montserrat(16px, $color-first, 500, 20px);
  }
}

.popup__button button:hover {
  opacity: 0.7;
}

.popup__button button:active {
  opacity: 0.5;
}

.popup__sending {
  padding-right: 18px;
  padding-top: 178px;
  display: none;

  @include respond-above ($breakpoints-mobile) {
    padding-right: 0;
    padding-top: 158px;
  }
}

.popup__sending button {
  @include button;
  @include montserrat(18px, $color-first, 600, 22px);

  width: 100%;
  padding: 15px;
  text-align: center;
  background-color: $color-third;
  cursor: pointer;
  border-radius: 8px;

  @include respond-above ($breakpoints-mobile) {
    @include montserrat(16px, $color-first, 500, 20px);
  }
}

.popup__sending button:hover {
  opacity: 0.7;
}

.popup__sending button:active {
  opacity: 0.5;
}

.popup__sending p {
  @include content;
  @include montserrat(18px, $color-second, 500, 29px);
  margin-bottom: 30px;
  text-align: center;
  position: relative;

  @include respond-above ($breakpoints-mobile) {
    font-size: 14px;
    line-height: 23px;
    margin-bottom: 20px;
  }
}

.popup__sending p:before {
  @include icon;

  position: absolute;
  top: -180px;
  left: 155px;
  width: 160px;
  height: 160px;
  border-radius: 50%;
  background: #f2f4f7;
  border: 1px solid $color-third;

  @include respond-above ($breakpoints-mobile) {
    width: 130px;
    height: 130px;
    top: -160px;
    left: 86px;
  }
}

.popup__sending p:after {
  @include icon;

  position: absolute;
  top: -137px;
  left: 198px;
  width: 70px;
  height: 70px;
  border-radius: 50%;
  background-color: $color-third;
  background-image: url("../img/check.svg");
  background-size: 36px 26px;
  background-position: 17px 20px;

  @include respond-above ($breakpoints-mobile) {
    width: 62px;
    height: 62px;
    background-size: 30px 32px;
    background-position: 15px 17px;
    top: -127px;
    left: 122px;
  }
}

.popup {
  position: fixed;
  background-color: rgba(0, 0, 15, 0.5);
  top: 0;
  left: 0;
  height: 100%;
  display: none;
  z-index: 105;
}
