@keyframes bounce {
  0% {
    opacity: 0
  }
  to {
    opacity: 1
  }
}

@keyframes shake {
  0%, to {
    margin-left: 0
  }
  10%, 30%, 50%, 70%, 90% {
    margin-left: -3px
  }
  20%, 40%, 60%, 80% {
    margin-left: 3px
  }
}

.main--hide {
  overflow: hidden;
}

.popup--show {
  display: flex;
  align-items: center;
  justify-content: center;
  animation: bounce 0.8s;
  overflow-y: auto;
  overflow-x: hidden;
  width: 100%;
}

.popup__wrap--error {
  animation: shake 0.8s
}

.show {
  display: block;
}

.hide {
  display: none;
}

.modal--show {
  display: block;
  animation: bounce 0.5s;
}

.electro-voice__left--icon:before {
  @include icon;

  display: block;
  position: relative;
  border: 10px solid transparent;
  border-top: 10px solid $color-first;
  left: 23px;
  bottom: -52px;
  z-index: 100;
  width: 10px;

  @include respond-above ($breakpoints-mobile) {
    left: 10px;
    bottom: -25px;
  }
}

.navigation--open {
  display: flex;

  @include respond-above ($breakpoints-tablet) {
    display: block;
    overflow-y: auto;
    overflow-x: hidden;
  }
}

.main--menu {
  @include respond-above ($breakpoints-tablet) {
    overflow: hidden;
  }
}

.page-header--menu {
  @include respond-above ($breakpoints-tablet) {
    min-height: 100vh;
  }
}
