.slider-second__wrapper {
  @include list;

  padding-bottom: 80px;

  @include respond-above ($breakpoints-tablet) {
    padding-bottom: 0;
  }
}

.slider-second {
  display: block;

  @include respond-above ($breakpoints-tablet) {
    display: none;
  }
}

.slider-second__slide span {
  @include montserrat(64px, $color-third, 800, 78px);
  letter-spacing: 0.02em;
  margin-bottom: 24px;
}

.slider-second__slide p {
  @include montserrat(14px, rgba(255, 255, 255, 0.7), 400, 22px);
}

.slider-second__slide {
  width: 290px;
  padding-top: 10px;
  padding-left: 35px;
  padding-right: 35px;
  border-right: 1px solid rgba(255, 255, 255, 0.1);
}

.slider-second__slide:nth-child(2) {
  width: 370px;
}

.slider-second__buttons {
  width: 105px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  position: relative;
  bottom: 30px;
  left: 25px;
}

.slider-second__buttons button {
  @include button;

  width: 30px;
  height: 30px;
  background-color: $color-third;
  border-radius: 50%;
  padding-left: 11px;
  padding-right: 8px;
}

.slider-second__buttons button:hover {
  background: rgba(186, 209, 255, 0.1);
}

.slider-second__buttons button:active {
  opacity: 0.2;
}

.slider-second__buttons button:first-child {
  padding-right: 11px;
  padding-left: 8px;
}

.slider-second__buttons .swiper-button-prev:after,
.slider-second__buttons .swiper-button-next:after {
  display: none;
}
